import React, { useState } from 'react';
import './about.css';
import Modal from "./modal";
import Video from "../assets/3d_video.mp4"
import { Link } from "gatsby";


const garelly_sources = [
  "img/project1.jpg",
  "img/project2.jpg",
  "img/project3.jpg",
  "img/project1.jpg",
  "img/project2.jpg",
  "img/project3.jpg",
  "img/project1.jpg",
  "img/project2.jpg",
  "img/project3.jpg",
  "img/project1.jpg",
  "img/project2.jpg",
  "img/project3.jpg",
];
const category_sources = [
  "A",
  "B",
  "C",
  "D",
  "E",
  "F",
  "G",
  "H",
  "I",
  "J",
  "K",
  "L",
]


const Project_image_garelly = () => {

  const [clicked_index, setClicked_index] = useState();
  
  const sendIndex = (index) => {
    var x = <Modal 
    clicked_index={index}
    garelly_sources={garelly_sources}
    category_sources={category_sources}
    close_modal={() => setClicked_index(null)}
    />;
    setClicked_index(x);
  }


  return(
    <>
      {clicked_index}
      
      <div className="garelly_container">
        <div className="inner_container">
          <h1>Our Projects</h1>
            <div className="border"></div>
            <div className="garelly">
              {garelly_sources.map((e, i) => 
                <div 
                  className="img_box"
                  key={i}
                  onClick={() => sendIndex(i)}
                >
                  <img 
                  src={e} 
                  alt="Project images"
                  />
                </div>
              )}
            </div>
            <div className="additional_space"></div>
            <button 
              className="show_more fontTwo">
                Show More  <i className="fas fa-caret-down fontTwo"></i>
            </button>
        </div>
      </div>
    </>
  );
};


const About = () => {
  
  return (
    <div id="about">
      <Project_image_garelly />
      
      <div className="aboutContainer">
        <div className="flex_right flexHeight">
            <video autoPlay loop preload="auto">
              <source src={Video} type="video/mp4"></source>
              <track default/>
            </video>
        </div>
        <div className="flex_left flexHeight">
          <div className="fontFour textContainer">
            <h2>Our Services</h2>
            <p>Our mission is your wish</p>
            <Link to="/service">
              <i className="fas fa-angle-double-left">
              </i>  Learn more 
            </Link>
          </div>
        </div>
      </div>
    </div>
  )
}

export default About;